import React from 'react';
import {GET_PERIOD_BASICS} from "../../../gql/queries/other";
import {useQuery} from '@apollo/client'
import {Autocomplete, TextField} from "@mui/material";

function PeriodSelection(props) {

    const [periodData, setPeriodData] = React.useState([])

    const {loading} = useQuery(GET_PERIOD_BASICS, {
        onCompleted: data => setPeriodData(data.periodWithValues.nodes)
    })

    const handleGetData = (data) => {
        props.getData({variables: {id: data.id}})
    }

    return (
        <Autocomplete
            blurOnSelect={true}
            loading={loading}
            sx={{width: '30em'}}
            size='small'
            disableClearable
            getOptionLabel={(option) =>
                `PERIOD NUMBER:  ${option.periodNumber}`
            }
            options={periodData}
            onChange={(_event, newValue) => handleGetData(newValue)}
            isOptionEqualToValue={(option, value) => option.id === value.id}
            renderInput={(params) => (
                <TextField
                    {...params}
                    variant='filled'
                    label='Period Number'
                    InputProps={{
                        ...params.InputProps,
                    }}
                />
            )}
        />
    );
}

export default PeriodSelection;