import React from 'react';
import {Bar, BarChart, CartesianGrid, Label, ResponsiveContainer, Tooltip, XAxis, YAxis} from 'recharts';
import {formatNumberNoDecimals} from "../../functions/commonFunctions";
import {Box} from "@mui/material";


function ValueByAreaChart(props) {
    const {data} = props
    return (
        <Box mb={1} sx={{borderStyle: 'solid', borderWidth: '1px', background: "#f2f2f2"}}>
            <h3 style={{textDecoration: 'underline'}}>VALUES BY AREA</h3>
            <ResponsiveContainer height={280}>
                <BarChart
                    data={data}
                    margin={{
                        top: 5,
                        right: 10,
                        left: 10,
                        bottom: 25,
                    }}
                >
                    <CartesianGrid strokeDasharray="3 3"/>
                    <XAxis dataKey="areaDescription">
                        <Label value="AREA" offset={0} position="bottom"/>
                    </XAxis>
                    <YAxis tickFormatter={data => formatNumberNoDecimals(data)} allowDataOverflow={true}/>
                    <Tooltip formatter={(value, name) => [formatNumberNoDecimals(value), 'Value Complete']}/>
                    <Bar dataKey="thisPeriodValue" fill="#001900"/>
                </BarChart>
            </ResponsiveContainer>
        </Box>
    );
}

export default ValueByAreaChart;