import React from 'react';
import {Box} from "@mui/material";
import ValueByAreaChart from "../../charts/ValueByAreaChart";
import ValueByOrderChart from "../../charts/ValueByOrderChart";
import ValueBySupervisorChart from "../../charts/ValueBySupervisorChart";

function PeriodValueCharts(props) {
    const {orderData, areaData, supervisorData} = props
    return (
        <Box
            sx={{
                border: 2,
                background: '#d0cece',
                p: 2,
                m: 1,
                display: 'flex',
                flexDirection: 'column',
                //   justifyContent: 'space-between'
            }}>
            <ValueByAreaChart data={areaData}/>
            <ValueByOrderChart data={orderData}/>
            <ValueBySupervisorChart data={supervisorData}/>
        </Box>
    );
}

export default PeriodValueCharts;