import React from 'react';
import PeriodSelection from "./PeriodSelection";
import {Box} from "@mui/material";
import {useLazyQuery} from "@apollo/client";
import {GET_PERIODS_WITH_VALUES} from "../../../gql/queries/other";
import PeriodValueSummary from "./PeriodValueSummary";

function PeriodValueEnquiry(props) {
    const [areaData, setAreaData] = React.useState([])
    const [dataLoaded, setDataLoaded] = React.useState(false);
    const [orderData, setOrderData] = React.useState([])
    const [supervisorData, setSupervisorData] = React.useState([])
    const [periodData, setPeriodData] = React.useState({})

    const [getPeriodValues] = useLazyQuery(GET_PERIODS_WITH_VALUES, {
        onCompleted: data => {
            setOrderData(data.period.periodValuesByOrderheaders.nodes)
            setSupervisorData(data.period.periodValuesBySupervisors.nodes)
            setPeriodData(data.period.periodWithValueById)
            setAreaData(data.period.periodValuesByAreas.nodes)
            setDataLoaded(true)
        }
    })

    return (
        <Box m={2}>
            <PeriodSelection getData={getPeriodValues}/>
            {dataLoaded && (
                <Box>
                    <PeriodValueSummary periodData={periodData} areaData={areaData} supervisorData={supervisorData}
                                        orderData={orderData}/>
                </Box>
            )}
        </Box>
    );
}

export default PeriodValueEnquiry;