import React from 'react';
import {Box} from "@mui/material";
import {formatDate} from "../../../functions/stringFormats";
import {formatNumberNoDecimals} from "../../../functions/numberFormats";
import PeriodValuesByProject from "./PeriodValuesByProject";
import PeriodValuesByArea from "./PeriodValuesByArea";
import PeriodValuesBySupervisor from "./PeriodValuesBySupervisor";
import PeriodValueCharts from "./PeriodValueCharts";

function PeriodValueSummary(props) {
    const {periodData, areaData, supervisorData, orderData} = props
    return (
        <Box
            sx={{background: '#e6e6e6'}}
            p={1}
            mt={2}

        >
            <Box
                display="grid" gridTemplateColumns="repeat(7, 1fr)"
                m={1}
                p={1}
                sx={{
                    border: 2,
                    fontWeight: 'bold',
                    textTransform: 'uppercase',
                    background: '#d0cece',
                    fontSize: 18,
                }}>
                <div>
                    PERIOD NUMBER:&nbsp; {props.periodData.periodNumber}
                </div>
                <div>
                    YEAR:&nbsp; {props.periodData.year}
                </div>
                <div>
                    WEEK:&nbsp; {props.periodData.week}
                </div>
                <div>
                    W/C DATE: &nbsp;{' '}
                    {formatDate(props.periodData.weekCommencingDate)}
                </div>
                <div>
                    W/E DATE: &nbsp;{' '}
                    {formatDate(props.periodData.weekEndingDate)}
                </div>
                <div>
                    PERIOD CLOSED: &nbsp;{' '}
                    {props.periodData.closed ? 'TRUE' : 'FALSE'}
                </div>
                <div>
                    PERIOD VALUE: &nbsp;{' '}
                    {props.periodData.closed ? formatNumberNoDecimals(props.periodData.worksValueClosed) : formatNumberNoDecimals(props.periodData.worksValueCurrent)}
                </div>
            </Box>
            <Box display="grid" gridTemplateColumns="repeat(4, 1fr)" gap={1}>
                <PeriodValuesByArea periodData={periodData} areaData={areaData}/>
                <PeriodValuesByProject periodData={periodData} orderData={orderData}/>
                <PeriodValuesBySupervisor periodData={periodData} supervisorData={supervisorData}/>
                <PeriodValueCharts areaData={areaData} orderData={orderData} supervisorData={supervisorData}/>
            </Box>
        </Box>
    );
}

export default PeriodValueSummary;