import React from 'react';
import {Box} from "@mui/material";
import {Bar, BarChart, CartesianGrid, Label, ResponsiveContainer, Tooltip, XAxis, YAxis} from "recharts";
import {formatNumberNoDecimals} from "../../functions/numberFormats";
import {getInitials} from "../../functions/stringFormats";

function ValueBySupervisorChart(props) {
  const data = props.data.map(item => ({...item, valueComplete: Number(item.valueComplete)}))

  return (
    <Box sx={{borderStyle: 'solid', borderWidth: '1px', background: "#f2f2f2"}}>
      <h3 style={{textDecoration: 'underline'}}>VALUES BY SUPERVISOR</h3>
      <ResponsiveContainer height={280}>
        <BarChart
          data={data}
          margin={{
            top: 5,
            right: 10,
            left: 10,
            bottom: 25,
          }}
        >
          <CartesianGrid strokeDasharray="3 3"/>
          <XAxis dataKey="supervisorName" tickFormatter={value => value && getInitials(value)}>
            <Label value="SUPERVISOR NAME" offset={0} position="bottom"/>
          </XAxis>
          <YAxis tickFormatter={data => formatNumberNoDecimals(data)} allowDataOverflow={true}/>
          <Tooltip formatter={(value, name) => [formatNumberNoDecimals(value), 'Value Complete']}/>
          <Bar dataKey="valueComplete" fill="#330033"/>
        </BarChart>
      </ResponsiveContainer>
    </Box>
  );
}

export default ValueBySupervisorChart;