/** @format */

import { Box, Typography } from '@mui/material';
import React from 'react';
import { formatNumberNoDecimals } from '../../../functions/numberFormats';

function PeriodValuesByProject(props) {
	return (
		<Box
			sx={{
				border: 2,
				background: '#d0cece',
				p: 2,
				m: 1,
			}}>
			<Box component='h3' m={0} mb={2} sx={{ textDecoration: 'underline' }}>
				ORDER VALUES
			</Box>
			{props.orderData.map((item) => (
				<Box key={item.id} sx={{ border: 2, background: 'white' }} mb={2} p={1}>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography fontWeight={'bold'}>PROJECT TITLE:</Typography>
						<Typography fontWeight={'bold'}>{item.projectTitle}</Typography>
					</Box>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography>AREA:</Typography>
						<Typography>{item.areaDescription}</Typography>
					</Box>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography>ORDER NUMBER:</Typography>
						<Typography>{item.orderNumber}</Typography>
					</Box>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography>LOCATIONS:</Typography>
						<Typography>
							{formatNumberNoDecimals(item.locationCount)}
						</Typography>
					</Box>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography>ITEMS:</Typography>
						<Typography>{formatNumberNoDecimals(item.itemCount)}</Typography>
					</Box>
					<Box sx={{ display: 'flex', justifyContent: 'space-between' }} m={1}>
						<Typography fontWeight={'bold'}>PERIOD VALUE:</Typography>
						<Typography fontWeight={'bold'} className={'accounting-total'}>
							{formatNumberNoDecimals(item.valueComplete)}
						</Typography>
					</Box>
				</Box>
			))}
		</Box>
	);
}

export default PeriodValuesByProject;
